'use client';

import { useEffect, useState } from 'react';
import axios from '@/lib/axios';
import socket from '@/lib/socket';
import { toast } from 'sonner';
import { Booking } from '@/types';

// enabled: when false, the hook will not fetch or open socket connections (useful for guest pages)
export default function useBookingsSync(initial: Booking[] = [], enabled: boolean = true) {
  const [bookings, setBookings] = useState<Booking[]>(initial);
  const [loading, setLoading] = useState<boolean>(enabled && !initial.length);

  useEffect(() => {
    if (!enabled) return; // don't start any background work when disabled

    let mounted = true;

    const fetchBookings = async () => {
      setLoading(true);
      try {
        const res = await axios.get('/api/bookings');
        if (!mounted) return;
        setBookings(res.data);
      } catch (e: any) {
        console.error(e);
        // Provide a clearer toast message for server errors
        toast.error(e?.response?.data?.message || 'Gagal memuat booking.');
      } finally {
        if (mounted) setLoading(false);
      }
    };

    fetchBookings();

    try { socket.connect(); } catch (e) {}

    const upsertBooking = (payload: any) => {
      setBookings(prev => {
        const idx = prev.findIndex(b => b.id === payload.id);
        if (idx === -1) return [payload, ...prev];
        const next = [...prev];
        next[idx] = payload;
        return next;
      });
    };

    const removeBooking = (payload: any) => {
      setBookings(prev => prev.filter(b => b.id !== payload.id));
    };

    socket.on('booking:created', upsertBooking);
    socket.on('booking:pending', upsertBooking);
    socket.on('booking:confirmed', upsertBooking);
    socket.on('booking:rejected', upsertBooking);
    socket.on('booking:updated', upsertBooking);
    socket.on('booking:deleted', removeBooking);

    return () => {
      mounted = false;
      socket.off('booking:created', upsertBooking);
      socket.off('booking:pending', upsertBooking);
      socket.off('booking:confirmed', upsertBooking);
      socket.off('booking:rejected', upsertBooking);
      socket.off('booking:updated', upsertBooking);
      socket.off('booking:deleted', removeBooking);
      try { socket.disconnect(); } catch(e) {}
    };
  }, [enabled]);

  const refresh = async () => {
    if (!enabled) return; // no-op when disabled
    setLoading(true);
    try {
      const res = await axios.get('/api/bookings');
      setBookings(res.data);
    } catch (e: any) {
      console.error(e);
      toast.error(e?.response?.data?.message || 'Gagal memuat booking.');
    } finally {
      setLoading(false);
    }
  };

  return { bookings, setBookings, loading, refresh };
}
